<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
       <el-col :span="6">
       </el-col>
       <el-col :span="6">
              <el-button type="primary" @click="addDialogVisible = true"></el-button>
       </el-col>
      </el-row>
      <el-table :data="this.content" border  stripe style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" width= '150' prop="name"></el-table-column>
        <el-table-column label="" prop="pictures" style="width: 50%;">
          <template scope="scope">
             <img v-for="item in scope.row.pictures" :src="item" class="swiperImg" />
          </template>
         </el-table-column>
        <el-table-column label="" width= '250' prop="details">
         <template scope="scope">
           <img  :src="scope.row.details" class="swiperImg" />
         </template>
        </el-table-column>
        <el-table-column label="" prop="genTime"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
              <el-button type="primary" size="middle" icon="el-icon-edit"  @click="showEditDialog(scope.row.id)" ></el-button>
          </template>
        </el-table-column>
        <el-table-column  width= '200' label="">
          <template slot-scope="scope">
             <el-button type="danger"  size="middle" icon="el-icon-delete" @click="removeShopById(scope.row.id)" ></el-button>
          </template>
      </el-table-column>
      </el-table>
      <el-pagination
        background
        :current-page="this.number"
        @current-change="handleCurrentChange"
        layout="prev, pager, next"
        :page-size ="10"
        :total="this.totalElements">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" :before-close="handleClose">
       <!--  -->
         <el-form ref="form2" :model="form2" label-width="80px">
           <el-form-item label="" label-width="120px">
           <el-input v-model="form2.name" name="names" style="width:360px;"></el-input>
       </el-form-item>
           <el-form-item label-width="120px" label="">
             <!--elementuiupload :before-upload="beforeUpload"> -->
             <el-upload
                   ref="detailUpload"
               class="upload-demo"
               action="/as"
               :limit= "1"
                   multiple
                   :http-request="handleDetailUpload"
               :auto-upload="false"
               
               list-type="picture">
               <el-button size="small" type="primary"></el-button>
                 <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
             </el-upload>
           </el-form-item>
     <el-form-item label-width="120px" label="(6)">
       <!--elementuiupload :before-upload="beforeUpload"> -->
       <el-upload
             ref="upload"
         class="upload-demo"
         action="/as"
         :limit= "6"
             multiple
             :http-request="handleUpload"
         :auto-upload="false"
         :on-remove="handleRemove"
         list-type="picture">
         <el-button size="small" type="primary"></el-button>
           <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
       </el-upload>
     </el-form-item>
     <el-form-item style="padding-top:20px;" label-width="120px" >
          <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%">
       <!--  -->
         <el-form ref="form2" :model="editForm" label-width="80px">
           <el-form-item label="" label-width="120px">
           <el-input v-model="editForm.name" name="names" style="width:360px;"></el-input>
       </el-form-item>
           <el-form-item label-width="120px" label="">
             <!--elementuiupload :before-upload="beforeUpload"> -->
             <el-upload
                   ref="detailUpload"
               class="upload-demo"
               action="/as"
               :limit= "1"
                   multiple
                   :http-request="handleDetailUpload"
               :auto-upload="false"
               
               list-type="picture">
               <el-button size="small" type="primary"></el-button>
                 <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
             </el-upload>
           </el-form-item>
     <el-form-item label-width="120px" label="(6)">
       <!--elementuiupload :before-upload="beforeUpload"> -->
       <el-upload
             ref="upload"
         class="upload-demo"
         action="/as"
         :limit= "6"
             multiple
             :http-request="handleUpload"
         :auto-upload="false"
         :on-remove="handleRemove"
         list-type="picture">
         <el-button size="small" type="primary"></el-button>
           <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
       </el-upload>
     </el-form-item>
     <el-form-item style="padding-top:20px;" label-width="120px" >
          <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    created() {
      this.token = window.sessionStorage.getItem('token');
      this.getShopList(1);
    },
    data() {
      return {
         token: '',
         addDialogVisible: false, //
         editDialogVisible: false, //
         picture: [],
         details: [],
         fileList: [],
 shopData: {
   name: '',
          token: '',
          picture: [],
          details: ''
 },
         form2: {
           name: ''
         },
         editForm: {
           name: '',
           picture: [],
           details: ''
         },
 params2:{},
        productList: [],
        totalElements: 0,
        number: 1,
        size: 10,
        page: 1,
        content: [],
         picture: '',
         details: ''
      }
     },
    methods: {

      //
      handleCurrentChange(val) {
         this.page = val;
         this.getShopList(val)
      },
      //
      getShopList(page) {
        let param = new FormData();
        param.append('token', this.token);
        param.append('page', page)
        axios({
          method: 'post',
          data: param,
          url: '/lightspace/productList'
        }).then((res) => {
          console.log(res)
          res? res = res.data.data: '';
          this.content = res.content;
          this.totalElements = res.totalElements;
          this.size = res.size;
          this.number = res.number + 1;
        }).catch((err) => {
          console.log(err)
        })
      },
      failResult(err){
        console.log(err)
      },
 handleRemove(file,filesList){
   this.fileList.delete(file);
   console.log(this.fileList)
 },
     beforeUpload(file){
      this.fileList.push(file);
 },
   handleUpload(raw){
      this.fileList.push(raw.file);
    },
    handleDetailUpload(raw) {
      this.details = raw.file;
    },
 handleRemove(file,filesList){
   this.param2.delete('file')
 },
     //
 async onSubmit(){//
     this.$refs.upload.submit();
     this.$refs.detailUpload.submit();
     let param = new FormData();
     param.append('token', this.token);
     param.append('name', this.form2.name);
     param.append('details', this.details);
     this.fileList.forEach(function (file) {
      param.append('picture', file); // 
           //Object
      })
     axios({
       method: 'post',
       data: param,
       url: '/lightspace/addProduct'
     }).then((res) => {
       if(res.data.status == 200) {
         this.addDialogVisible = false;
         this.form2.name = '';
         this.details = [];
         this.fileList = [];
         this.getShopList();
       }
     }).catch((err) => {
       console.log(err)
     })
     },
    //
     handleClose() {
      this.addDialogVisible = false;
      this.form2.name = '';
      this.details = [];
      this.fileList = [];
     },
     //
     async removeShopById(id) {
         const confirmResult = await this.$confirm(', ?', '', {
         confirmButtonText: '',
         cancelButtonText: '',
         type: 'warning'
         }).catch(err => err)
         if(confirmResult !== 'confirm') {
             return this.$message.info('')
         }
         let param = new URLSearchParams();
         param.append('token', this.token);
         param.append('id', id)
         axios({
             method: 'post',
             url: '/lightspace/deleteProduct',
             data: param
         }).then(this.handleDeleteProSucc.bind(this))
         .catch(this.handleDeleteProErr.bind(this))
     },
     handleDeleteProSucc(res) {
         if(res.data.status === 10204) {
             this.$message.error(res.data.msg);
             this.$router.push('/login');
         } else if(res.data.status == 200) {
            this.$message.success('');
            this.getShopList(1);
            // this.recordList = res.data.data;
            // const totalPage = Math.ceil(this.recordList.length / this.pageSize) // 
            // this.currentPage = this.currentPage > totalPage ? totalPage : this.currentPage
            // this.currentPage = this.currentPage < 1 ? 1 : this.currentPage
         }
     },
     handleDeleteProErr(err) {
         console.log(err)
     },
     showEditDialog(id){
       console.log(id)
       let param = new FormData();
       param.append('token', this.token);
       param.append('id', id);
       axios({
         method: 'post',
         data: param,
         url: '/lightspace/editProduct'
       }).then((res) => {
         console.log(res)
         this.editDialogVisible = true;
         this.editForm = res.data.data;
       }).catch((err) => {
         console.log(err)
       })
     }
    }
    }
</script>

<style>
  input[type="file"] {
          display: none;
      }

      .avatar-uploader .el-upload {
          border: 1px dashed #d9d9d9;
          border-radius: 6px;
          cursor: pointer;
          position: relative;
          overflow: hidden;
      }

      .avatar-uploader .el-upload:hover {
          border-color: #409EFF;
      }

      .avatar-uploader-icon {
          font-size: 28px;
          color: #8c939d;
          width: 178px;
          height: 178px;
          line-height: 178px;
          text-align: center;
      }

      .avatar {
          width: 178px;
          height: 178px;
          display: block;
      }
      .avatar-uploader {
        border: 1px dashed #d9d9d9;
        border-radius: 6px;
        cursor: pointer;
        width: 20rpx;
        height: 20rpx;
      }
  .swiperImg {
    width: 16%;
    height: 50px;
    margin: 5px;
    display: inline-block;
  }
</style>



